body,
.ss-text {
  font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  margin: 0 0 1.2rem;
  line-height: 1.65;
}

.ss-text-upper {
  text-transform: uppercase;
}

.ss-text-no-margin {
  margin-top: 0;
  margin-bottom: 0;
}

.ss-text-align-right {
  text-align: right;
}

.ss-text-align-left {
  text-align: left;
}

.ss-text-align-center {
  text-align: center;
}

.ss-text-scale-none {
  font-size: 1rem;
}

.ss-text-scale-xs {
  font-size: 0.8rem;
}

.ss-text-scale-sm {
  font-size: 0.9rem;
}

.ss-text-scale-md {
  font-size: 1.25rem;
}

.ss-text-scale-lg {
  font-size: 1.75rem;
}

.ss-text-scale-xl {
  font-size: 2.25rem;
}

.ss-text-scale-xxl {
  font-size: 3rem;
}

